import mqtt from 'mqtt'

/**
 * @type { (topics: string[], { resolve, reject }: {resolve: (topic:string)=>void;reject: (err:any,topic:string|null)=>void;}, onDatas: {[key:string]:(data:string)=>void}) => () => void}
 */

export const initMqtt = (topics, { resolve, reject }, onDatas) => {
  // clinet  ( activemq ws 61614 )
  const client = mqtt.connect('ws://127.0.0.1:38080/fr/imserver/u1', {
    clientId:
      'mqttjs_' +
      Math.random()
        .toString(16)
        .substr(2, 8),
    //  start
    protocolId: 'mqtt',
    protocolVersion: 3,
  })

  // 
  client.on('connect', () => {
    // topics
    topics.forEach(topic => {
      client.subscribe(topic, function(err) {
        if (!err) {
          // 
          resolve(topic)
        } else {
          // 
          reject(err, topic)
        }
      })
    })
  })

  // 
  client.on('error', function(err) {
    client.end()
    reject(err, null)
  })

  // client
  client.on('message', (topic, message) => {
    // message is Buffer
    const data = message.toString()
    onDatas[topic](data)
  })

  // 
  return () => {
    client.end()
  }
}



